body {
    background-color: rgb(233, 246, 230);
}

.content {
position: absolute;
top: 50%;
left: 50%;
transform: translate(-50%, -50%);
font-size: 35px;
line-height: 40px;
color: #000000;
max-width: 360px;

    &__container {
        font-weight: 600;
        overflow: hidden;
        height: 40px;
        padding: 0 40px;

        &:before {
        content: '[';
        left: 0;
        }

        &:after {
        content: ']';
        position: absolute;
        right: 0;
        }

        &:after, &:before {
        position: absolute;
        top: 0;
        
        color: rgb(4, 116, 56);
        font-size: 42px;
        line-height: 40px;
        
        -webkit-animation-name: opacity;
        -webkit-animation-duration: 2s;
        -webkit-animation-iteration-count: infinite;
        animation-name: opacity;
        animation-duration: 2s;
        animation-iteration-count: infinite;
        }

        &__text {
        display: inline;
        float: left;
        margin: 0;
        }

        &__list {
        margin: 0;
        padding-left: 110px;
        text-align: left;
        list-style: none;
        
        -webkit-animation-name: change;
        -webkit-animation-duration: 10s;
        -webkit-animation-iteration-count: infinite;
        animation-name: change;
        animation-duration: 10s;
        animation-iteration-count: infinite;

            &__item {
                line-height: 40px;
                margin: 0;
                padding: 0;
            }
        }
    }
}

.subcontent {
    margin-top: 10%;
    font-size: 35px;
    line-height: 40px;
    color: #000000;

    &__container {
        margin-top: 5%;
        font-weight: 600;
        overflow: hidden;
        height: 100%;
        padding: 0 40px;

        &__text {
            font-size: xx-large;
            display: inline;
            margin: 0;
        }

    }
}

@-webkit-keyframes opacity {
0%, 100% {opacity:0;}
50% {opacity:1;}
}

@-webkit-keyframes change {
0%, 12.66%, 100% {transform:translate3d(0,0,0);}
16.66%, 29.32% {transform:translate3d(0,-25%,0);}
33.32%,45.98% {transform:translate3d(0,-50%,0);}
49.98%,62.64% {transform:translate3d(0,-75%,0);}
66.64%,79.3% {transform:translate3d(0,-50%,0);}
83.3%,95.96% {transform:translate3d(0,-25%,0);}
}

